.accordion__button {
  display: none;
}
@media (max-width: 769px) {
  .accordion {
    margin: 0 auto 0 auto;
  }
  .accordion .accordion {
    width: 90%;
    margin: 0 auto;
  }
  .accordion__element:last-child {
    margin-bottom: 0;
    border-bottom: 1px solid rgba(248, 253, 255, 0.698);
  }
  .accordion__element {
    border-top: 1px solid rgba(248, 253, 255, 0.698);
    position: relative;
  }
  .accordion__element::before, .accordion__element::after {
    position: absolute;
    top: 27px;
    right: 28px;
    content: "";
    display: block;
    width: 15px;
    height: 1px;
    background-color: #ffffff;
    transition: transform 1s;
  }
  .accordion__element::after {
    transform: rotate(-90deg);
  }
  .accordion__element.is-active::after {
    transform: rotate(-180deg);
  }
  .accordion__element-title {
    display: none;
  }
  .accordion__element.is-active > .accordion__button {
    opacity: 0.5;
  }
  .accordion__button {
    display: block;
    width: 100%;
    min-height: 60px;
    padding: 10px 25px;
    padding-right: 160px;
    font-size: 15px;
    line-height: 16px;
    text-transform: uppercase;
    text-align: left;
    background-color: transparent;
    border: none;
    color: #ffffff;
    cursor: pointer;
  }
  .accordion__wrapper {
    padding: 10px;
  }
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

