.accordion {

  &__button {
    display: none;
  }

  @media (max-width: 769px) {
      //max-width: 500px;
  margin: 0 auto 0 auto;

  & & {
    width: 90%;
    margin: 0 auto;
  }

  &__element:last-child {
    margin-bottom: 0;

    border-bottom: 1px solid rgba(248, 253, 255, 0.698);
  }

  &__element {
    border-top: 1px solid rgba(248, 253, 255, 0.698);
    position: relative;

    &::before,
    &::after {
      position: absolute;
      top: 27px;
      right: 28px;
      content: "";

      display: block;
      width: 15px;
      height: 1px;

      background-color: #ffffff;

      transition: transform 1s;
    }
    &::after {
      transform: rotate(-90deg);
    }
  }

  &__element.is-active {
    &::after {
      transform: rotate(-180deg);
    }
  }

  &__element-title {
    display: none;
  }

  &__element.is-active > &__button {
    opacity: 0.5;
  }

  &__button {
    display: block;
    width: 100%;
    min-height: 60px;
    padding: 10px 25px;
    padding-right: 160px;

    font-size: 15px;
    line-height: 16px;
    text-transform: uppercase;
    text-align: left;

    background-color: transparent;
    border: none;
    color: #ffffff;

    cursor: pointer;
  }

  &__wrapper {
    padding: 10px;
  }
  }
}
